.header {
      height: 10px;
      width: 100%;
      display: flex;
      justify-content: flex-end;
    
      .logo-container {
        height: 100%;
        width: 70px;
        padding: 25px;
      }
    
      .options {
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
    
        .option {
          padding: 10px 15px;
          cursor: pointer;
        }
      }
    }